.viewer section {
  width: 100%;
  margin-bottom: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.viewer h4 {
  font-size: 22px;
  font-weight: bold;
}
.viewer .emotion-img-wrapper {
  background-color: #ececec;
  width: 250px;
  height: 250px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.viewer .emotion-img-wrapper-1 {
  background-color: #64c964;
}
.viewer .emotion-img-wrapper-2 {
  background-color: #9dd772;
}
.viewer .emotion-img-wrapper-3 {
  background-color: #fdce17;
}
.viewer .emotion-img-wrapper-4 {
  background-color: #fd8446;
}
.viewer .emotion-img-wrapper-5 {
  background-color: #fd565f;
}
.viewer .emotion-des {
  font-size: 25px;
  color: #fff;
}
.viewer .content-wrapper {
  width: 100%;
  background-color: #ececec;
  border-radius: 7px;
  word-break: keep-all;
  overflow-wrap: break-word;
  white-space: pre-line;
}
.viewer .content-wrapper p {
  padding: 20px;
  text-align: left;
  font-size: 20px;
  font-family: "yeon Sung";
  font-weight: 400;
  line-height: 2.5;
}