.editor-section {
  margin-bottom: 40px;
  padding: 0 1rem;
}
.editor h4 {
  font-size: 22px;
  font-weight: bold;
}
.editor input, textarea {
  border: none;
  background-color: #ececec;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 20px;
  font-family: "Nanum Pen Script";
}
.editor input {
  padding: 10px;
  cursor: pointer;
  outline: none;
}
.editor textarea {
  width: 100%;
  min-height: 200px;
  box-sizing: border-box;
  resize: none;
  outline: none;
}
.editor .button-section {
  display: flex;
  justify-content: space-between;
}
.editor .emotion-list-wrapper {
  display: flex;
  justify-content: space-around;
  gap: 2%;
  cursor: pointer;
}