.diary-list .menu-wrapper {
  margin: 20px 0 30px 0;
  display: flex;
  justify-content: space-between;
}
.diary-list .left-col select {
  margin-right: 10px;
  border: none;
  border-radius: 5px;
  background-color: #ececec;
  padding: 10px 20px;
  cursor: pointer;
  font-family: "Nanum Pen Script";
  font-size: 18px;
}
.diary-list .menu-wrapper .right-col {
  flex-grow: 1;
}
.diary-list .menu-wrapper .right-col button {
  width: 100%;
}