.diary-item {
  padding: 15px 0;
  border-bottom: 1px solid #e2e2e2;
  display: flex;
  justify-content: space-between;
}
.diary-item .img-section {
  cursor: pointer;
  min-width: 120px;
  height: 70px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
}
.diary-item .img-section-1 {
  background-color: #64c964;
}
.diary-item .img-section-2 {
  background-color: #9dd772;
}
.diary-item .img-section-3 {
  background-color: #fdce17;
}
.diary-item .img-section-4 {
  background-color: #fd8446;
}
.diary-item .img-section-5 {
  background-color: #fd565f;
}
.diary-item .img-section img {
  width: 50%;
}
.diary-item .info-section {
  flex-grow: 1;
  margin-left: 20px;
  cursor: pointer;
}
.diary-item .info-section .date-wrapper {
  font-weight: bold;
  font-size: 23px;
  margin-bottom: 5px;
}
.diary-item .info-section .content-wrapper {
  font-size: 18px;
}