.Button {
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 18px;
  white-space: nowrap;
  font-family: "Nanum Pen Script";
}
.Button_default {
  background-color: #ececec;
  color: #000;
}
.Button_positive {
  background-color: #64c964;
  color: #fff;
}
.Button_negative {
  background-color: #fd565f;
  color: #fff;
}