.header {
  padding: 20px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #e2e2e2;
} 
.header div {
  display: flex;
}
.header button {
  font-family: "Nanum Pen Script";
}
.header .header-title {
  width: 50%;
  justify-content: center;
  font-size: 25px;
}
.header .header-left {
  width: 25%;
  justify-content: start;
}
.header .header-right {
  width: 25%;
  justify-content: end;
}